.my-chart-tooltip {
  border-radius: 6px;
  background: #7b40f2;
  box-shadow: 0px 13px 38px rgba(0, 0, 0, 0.05);
  color: white;
  padding: 4px 20px;
  line-height: 18px;
  font-size: 12px;
  font-weight: 500;
}

.color-primary {
  color: #7b40f2;
}
